import React from "react";
import Modal from "react-bootstrap/Modal";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Image from "react-bootstrap/Image";
import Button from "react-bootstrap/Button";
import Card from "react-bootstrap/Card";
import Moment from "moment";

import Url from "../api/url";

const getOrientationName = (orientation) => {
  if (orientation === 1) {
    return "Horizontal";
  } else if (orientation === 2) {
    return "Vertical";
  } else {
    return "Square";
  }
};

const readableBytes = (bytes) => {
  var i = Math.floor(Math.log(bytes) / Math.log(1024)),
    sizes = ["B", "KB", "MB", "GB", "TB", "PB", "EB", "ZB", "YB"];
  return (bytes / Math.pow(1024, i)).toFixed(2) * 1 + " " + sizes[i];
};

function ModalImgDetail({
  showModalImageDetail,
  handleCloseMID,
  imgDetail,
  handleEditImage,
}) {
  console.log(imgDetail);
  return (
    <Modal
      show={showModalImageDetail}
      size="xl"
      onHide={handleCloseMID}
      style={{ maxHeight: "100%" }}
    >
      <Modal.Header closeButton>
        <Modal.Title>Image detail</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Row>
          <Col sm={8}>
            <Image
              style={{ maxHeight: "80%", maxWidth: "100%" }}
              src={imgDetail["image"]}
            />
          </Col>
          <Col>
            <Card
              bg="secondary"
              style={{ width: "18rem" }}
              className="mb-2"
              text="white"
            >
              <Card.Header>
                {imgDetail["width"]} x {imgDetail["height"]} /
                {readableBytes(imgDetail["size"])} -
                {getOrientationName(imgDetail["orientation"])}
              </Card.Header>
              <Card.Body>
                <Card.Title>{imgDetail["country"]}</Card.Title>
                <Card.Text>
                  {imgDetail["title"]}
                  <br />
                  Date: {imgDetail["date"]}
                  <br />
                  Author: {imgDetail["author"]}
                  <br />
                  Uploaded At:
                  {Moment(imgDetail["uploaded_at"]).format(
                    "YYYY-MM-DD - HH:MM"
                  )}
                  <br />
                  User: {imgDetail["user"]}
                  <br />
                  Image: {imgDetail["image"]}
                </Card.Text>
              </Card.Body>
            </Card>
            <p>Id: {imgDetail["id"]}</p>
          </Col>
        </Row>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="primary" onClick={handleEditImage}>
          Edit
        </Button>
      </Modal.Footer>
    </Modal>
  );
}

export default ModalImgDetail;
